.container {
  display: flex;
  flex: 1;
  flex-direction: row;
  --table-title-lh: 20px;
  --table-title-mb: 16px;
}

.main {
  flex-grow: 1;
  flex-shrink: 0;
  margin-right: 64px;
}

.tableTitle {
  font-size: 16px;
  line-height: var(--table-title-lh);
  font-weight: bold;
  margin-bottom: var(--table-title-mb);
}

.tableHeaderContainer {
  padding: 8px 16px;
  background-color: #dae8f7;
  font-weight: 500;

  > .tableHeaderCell {
    display: inline-block;
  }
}

.summaryContainer {
  position: relative;
  top: calc(var(--table-title-lh) + var(--table-title-mb));
}

.redBox {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-right: 29px;
  text-align: center;
  background-color: #d53d4a;
  font-family: Montserrat;
  font-style: normal;
  font-size: 14px;
  color: #f1f1f1;
}
.redBox1 {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-right: 29px;
  margin-left: 40px;
  text-align: center;
  background-color: #d53d4a;
  font-family: Montserrat;
  font-style: normal;
  font-size: 14px;
  color: #f1f1f1;
}
.hr {
  border: 1px solid #f1f1f1;
}
.tableHead {
  margin-right: 65px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  color: #3c4652;
}
.tableHeadRight {
  margin-right: 65px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  color: #3c4652;
}
.tableRight {
  background: #f1f1f1;
  font-family: Montserrat;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 16px;
}
