.container {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  color: white;
  height: var(--header-height);
  overflow: hidden;
  z-index: 100;
}

.container > nav {
  background-color: #2f3742;
  flex-grow: 1;
  border-bottom: solid 1px #1f222a;
}

.container > nav > ul {
  display: flex;
  white-space: nowrap;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.container > nav > ul > li {
  display: flex;
  align-items: center;
  padding: 14px 30px;
  border-right: solid 1px #252c35;
}

.logo {
  width: 128px;
  margin-left: 16px;
}

.objectSplitter {
  width: 4px;
  height: 32px;
  background-color: var(--danger-300);
}

.objectBtn {
  color: white;
}
